<template>
  <div>
    <div class="mb-5 company-top">
      <!-- <el-button class="mr-10" size="small">操作说明</el-button> -->
      <!-- <router-link to="/admin/company">
        <el-button class="mr-10">企业管理</el-button>
      </router-link>-->
      <div class="header-span fl mr-20">
        <span>开票管理</span>
      </div>
      <el-button class="mr-10" size="mini" @click="addcompany" type="success">企业新增</el-button>
      <!-- <router-link to="/admin/business">
        <el-button class="mr-10">业务管理</el-button>
      </router-link>-->
    </div>
    <div v-show="showCompanyadd">
      <Popups title="企业数据设置">
        <div slot="close" @click="showCompanyaddoff">
          <el-icon class="el-icon-close"/>
        </div>
        <div slot="content">
          <div class="preset">
            <!--企业基本信息设置-->
            <div class="table-content" style="height: 458px;overflow-y: auto; padding: 20px 20px">
              <el-form
                ref="companyInfo"
                :model="companyInfo"
                :rules="rules"
                label-width="120px"
                class="companyInfo"
              >
                <el-form-item prop="trade_name" label="企业名称">
                  <el-input
                    type="text"
                    v-model="companyInfo.trade_name"
                    placeholder="例：山东问云机械有限公司"
                  />
                </el-form-item>
                <el-form-item prop="firm_code" label="纳税人识别号">
                  <el-input
                    type="text"
                    v-model="companyInfo.firm_code"
                    placeholder="例：91370305755666003U"
                  />
                </el-form-item>
                <el-form-item label="省份">
                  <el-input type="text" v-model="companyInfo.province" placeholder="例：山东"/>
                </el-form-item>
                <el-form-item label="默认初始用户名">
                  <el-input type="text" v-model="companyInfo.user_name" placeholder="例：管理员"/>
                </el-form-item>
                <el-form-item label="密码">
                  <el-input type="text" v-model="companyInfo.password" placeholder="例：123456"/>
                </el-form-item>
                <el-form-item label="口令">
                  <el-input type="text" v-model="companyInfo.token" placeholder="例：12345678"/>
                </el-form-item>
                <el-form-item label="默认初始用户备注">
                  <el-input type="text" v-model="companyInfo.remark" placeholder="例：备注"/>
                </el-form-item>
                <el-form-item label="公司地址">
                  <el-input
                    type="text"
                    v-model="companyInfo.address"
                    placeholder="例：山东省淄博市张店区德胜路85号"
                  />
                </el-form-item>
                <el-form-item label="联系电话">
                  <el-input type="text" v-model="companyInfo.telno" placeholder="例：3918359"/>
                </el-form-item>
                <el-form-item label="开户行地址">
                  <el-input
                    type="text"
                    v-model="companyInfo.bank_address"
                    placeholder="例：中国建设银行德胜路支行"
                  />
                </el-form-item>
                <el-form-item label="银行账号">
                  <el-input type="text" v-model="companyInfo.bank_code" placeholder="例：银行账号"/>
                </el-form-item>
                <el-form-item label="专票代码">
                  <el-input
                    type="text"
                    v-model="companyInfo.zhuan_code"
                    placeholder="长度为10位 例：3700114140"
                  />
                </el-form-item>
                <el-form-item label="专票代码起始号码">
                  <el-input
                    type="text"
                    v-model="companyInfo.zhuan_code_start"
                    placeholder="长度为8位 例：3700114140"
                  />
                </el-form-item>
                <el-form-item label="专票代码结束号码">
                  <el-input
                    type="text"
                    v-model="companyInfo.zhuan_code_end"
                    placeholder="例：3700114140"
                  />
                </el-form-item>
                <el-form-item label="专票限额">
                  <el-input type="text" v-model="companyInfo.zhuan_xiane" placeholder="例：10000.00"/>
                </el-form-item>

                <el-form-item label="普票代码">
                  <el-input
                    type="text"
                    v-model="companyInfo.po_code"
                    placeholder="长度为10位 例：3700114140"
                  />
                </el-form-item>
                <el-form-item label="普票代码起始号码">
                  <el-input
                    type="text"
                    v-model="companyInfo.po_code_start"
                    placeholder="长度为8位 例：3700114140"
                  />
                </el-form-item>
                <el-form-item label="普票代码结束号码">
                  <el-input
                    type="text"
                    v-model="companyInfo.po_code_end"
                    placeholder="例：3700114140"
                  />
                </el-form-item>
                <el-form-item label="普票限额">
                  <el-input type="text" v-model="companyInfo.po_xiane" placeholder="例：10000.00"/>
                </el-form-item>

                <el-form-item prop="remark" label="开票时间">
                  <el-input
                    type="text"
                    v-model="companyInfo.kaipiao_time"
                    placeholder="例：2018-04-01"
                  />
                </el-form-item>
                <el-form-item prop="remark" label="领票时间">
                  <el-input
                    type="text"
                    v-model="companyInfo.lingpiao_time"
                    placeholder="例：2018-04-01"
                  />
                </el-form-item>
                <el-form-item prop="remark" label="锁死日期">
                  <el-input type="text" v-model="companyInfo.lock_day" placeholder="例：2018-04-01"/>
                </el-form-item>
                <el-form-item prop="remark" label="上次报税日期">
                  <el-input
                    type="text"
                    v-model="companyInfo.baoshui_day_start"
                    placeholder="例：2018-04-01"
                  />
                </el-form-item>
                <el-form-item prop="remark" label="抄税起始日期">
                  <el-input
                    type="text"
                    v-model="companyInfo.chaoshui_day_start"
                    placeholder="例：2018-04-01"
                  />
                </el-form-item>
                <el-form-item prop="remark" label="红字流水起始号码">
                  <el-input
                    type="text"
                    v-model="companyInfo.red_code_start"
                    placeholder="例：661558640110161227158056。长度14位，不以0开头"
                  />
                </el-form-item>
                <el-form-item prop="remark" label="是否小规模企业">
                  <el-input type="text" v-model="companyInfo.is_mini_company" placeholder="例：否"/>
                </el-form-item>
                <div class="text-center">
                  <el-button @click="saveCompanyInfo('companyInfo')" size="mini" type="success">保 存</el-button>
                  <el-button
                    class="green-btn"
                    @click="handleReset('companyInfo')"
                    size="mini"
                    style="margin-left: 8px"
                  >重 置
                  </el-button>
                </div>
              </el-form>
            </div>
          </div>
        </div>
      </Popups>
    </div>
    <div class="company-info" v-show="isCompanyHave">
      <div class="row justify-content-between">
        <div class="float-left company-title">
          <strong class="company-name">企业名称：{{companyNow.trade_name}}</strong>
        </div>
        <div class="float-right text-center company-operating">
          <a class="mr-10" @click="addBusiness">新增业务</a>
          <a @click="toPracticalAnswer" class="mr-10">答案绑定</a>
          <a class="mr-10" @click="editCompany">编辑</a>
          <a class="mr-10" @click="deleteCompany">删除</a>
        </div>
      </div>
    </div>
    <div class="table-responsive" v-show="isCompanyHave">
      <el-table
        border
        :data="tableData"
        size="small"
        style="width: 100%;"
        header-row-class-name="tableHead"
        cell-class-name="business-cell"
      >
        <el-table-column
          v-for="(item ,i) in tableTitle"
          :key="i"
          :prop="item.key"
          :label="item.title"
          :width="item.width"
        ></el-table-column>
        <el-table-column label="操作" width="150" align="center" header-align="center">
          <template slot-scope="scope">
            <!-- <el-button size="mini" @click="editorBusiness(scope.$index, scope.row)">编辑</el-button> -->
            <a class="mh-5" @click="editorBusiness(scope.$index, scope.row)">编辑</a>
            <!-- <el-button size="mini" type="danger" @click="deleteBusiness(scope.$index, scope.row)">删除</el-button> -->
            <a class="mh-5" @click="deleteBusiness(scope.$index, scope.row)">删除</a>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="mt-15 text-center" v-show="isCompanyHave">
      <el-pagination
        layout="prev, pager, next"
        :total="dataCount"
        :page-size="1"
        @current-change="changepage"
        class="text-center"
      ></el-pagination>
    </div>
    <div v-show="showBusinessadd">
      <Popups title="业务数据设置" width="500" height="450">
        <div slot="close" @click="showBusinessaddoff">
          <el-icon class="el-icon-close"/>
        </div>
        <div slot="content">
          <div class="preset">
            <!--企业基本信息设置-->
            <div
              class="table-content"
              style="height: 450px;overflow-y: auto; width = 500px; padding:20px"
            >
              <el-form
                ref="businessInfo"
                label-width="80px"
                :model="businessInfo"
                :rules="companyRuleValidate"
                class="companyInfo"
              >
                <el-form-item prop="content" label="业务内容">
                  <el-input type="textarea" v-model="businessInfo.content" placeholder="业务描述"/>
                </el-form-item>
                <!-- <el-form-item prop="requ" label="分值">
                  <el-input type="text" v-model="businessInfo.grade" placeholder="例：10%" />
                </el-form-item>-->
                <el-form-item prop="text_parse" label="文字解析">
                  <el-input type="textarea" v-model="businessInfo.text_parse" placeholder="文字解析"/>
                </el-form-item>
                <el-form-item label="视频解析">
                  <el-upload action="//jsonplaceholder.typicode.com/posts/">
                    <el-button type="success" size="mini">
                      上传
                      <i class="el-icon-view el-icon--right"></i>
                    </el-button>
                  </el-upload>
                </el-form-item>
                <el-form-item label="材料上传">
                  <el-upload action="//jsonplaceholder.typicode.com/posts/">
                    <el-button type="success" size="mini">
                      上传
                      <i class="el-icon-upload el-icon--right"></i>
                    </el-button>
                  </el-upload>
                </el-form-item>
                <div
                  class="text-center"
                  style="position:absolute;bottom:30px;left:50%;transform:translateX(-50%)"
                >
                  <el-button type="success" size="mini" @click="submitBusiness('businessInfo')">保 存</el-button>
                  <el-button style="margin-left: 8px" class="green-btn" size="mini">重 置</el-button>
                </div>
              </el-form>
            </div>
          </div>
        </div>
      </Popups>
    </div>

    <span
      v-if="!isCompanyHave"
      class="el-table__empty-text"
      style="position:relative;left:48%;transform:translateX(-50%)"
    >暂无数据</span>
  </div>
</template>

<script>
  import Popups from '../compnents/pop-ups-model' // 模拟弹窗组件
  import { popData, popMethod } from '@/until/adminMethods/pop'
  import dateHelper from '@/until/dateHelper'
  import { defultNull } from '@/until/dataHelper'

  export default {
    name: 'Material',
    // mixins: [common],
    components: {
      Popups
    },
    data () {
      return {
        // 状态依据
        ...popData,
        businessInfo: {},
        practicalAnswer: true,
        rules: {
          trade_name: [
            { required: true, message: '必填项必须要填写', trigger: 'blur' }
          ],
          firm_code: [
            { required: true, message: '必填项必须要填写', trigger: 'blur' }
          ]
        },
        index: 0, // 用于控制编辑的第几个业务
        // 业务表格数据区
        tableTitle: [
          {
            title: '业务内容',
            key: 'content'
          }
        ],
        tableData: [], // 业务数据集合
        // 企业分页数据
        ajaxHistoryData: [],
        // 初始化信息总条数
        dataCount: 1,
        // 每页显示多少条
        pageSize: 1,
        // 当前页码
        page: 1,
        historyData: [], // 当前企业键值
        companyNow: {},
        // 表数据预设状态
        tableCode: '', //加载哪张表的依据
        // 企业信息新增/修改依据
        iscompanyEditor: false,
        // 企业信息表单
        companyInfo: {},
        // 企业信息表单验证规则
        companyRuleValidate: {
          content: [
            { required: true, message: '必填项不能为空', trigger: 'blur' }
          ],
          text_parse: [
            { required: true, message: '必填项不能为空', trigger: 'blur' }
          ]
        },
        isCompanyHave: false
      }
    },
    created () {
      this.getInfo(1)
    },
    computed: {},
    watch: {
      showBusinessadd (val) {
        if (!val) {
          this.businessInfo = this.$options.data().businessInfo
        }
      },
      showCompanyadd (val) {
        if (!val) {
          this.companyInfo = this.$options.data().companyInfo
          this.companyStatus = true
        }
      },
      companyNow (val) {
        if (val) {
          this.isCompanyHave = true
        } else {
          this.isCompanyHave = false
        }
      }
    },
    methods: {
      ...popMethod,
      ...dateHelper,
      editCompany () { //修改公司
        this.showCompanyadd = true
        this.companyStatus = false
        this.companyInfo = { ...this.companyNow }
      },
      deleteCompany () { //删除公司
        app._delete(this, `admin/kp/company/del/${this.companyNow.id}`, { id: this.companyNow.id }, (res) => {
          this.getInfo(1)
        })
      },
      getInfo (count) { //获取公司和公司下的业务信息
        app._post(this, 'admin/kp/company/list?page_size=1&page=' + count, {}, (res) => {
          if (res.data.data.data.length === 0) {
            this.companyNow = false
            return
          } else {
            this.companyNow = res.data.data.data[0]
            this.tableData = res.data.data.data[0].business
            this.dataCount = res.data.data.total
          }
        })
      },
      saveCompanyInfo (name) { // 保存企业信息
        let defaultInfo = {
          province: '山东',
          user_name: '管理员',
          password: '123456',
          token: '12345678',
          remark: '备注',
          address: '山东省淄博市张店区',
          telno: '3918359',
          bank_address: '中国建设银行德胜路支行',
          bank_code: '622848154789651415687',
          zhuan_code: '3700114140',
          zhuan_code_start: '3700114140',
          zhuan_code_end: '3700114240',
          zhuan_xiane: '10000.00',
          po_code: '5500114140',
          po_code_start: '03279189',
          po_code_end: '03279213',
          po_xiane: '10000.00',
          kaipiao_time: '2018-04-01',
          lingpiao_time: '2018-04-15',
          lock_day: '2018-05-01',
          baoshui_day_start: '2018-03-01',
          chaoshui_day_start: '2018-05-01',
          red_code_start: '6615011016',
          is_mini_company: '否'
        }
        var companyInfoData = { ...this.companyInfo }
        companyInfoData = defultNull(companyInfoData, defaultInfo)
        this.$refs[name].validate((valid) => {
          if (valid) {
            if (this.companyStatus) {
              app._post(this, 'admin/kp/company/add', companyInfoData, (res) => {
                this.showCompanyadd = false
                this.getInfo(1)
              })
            } else {
              app._post(this, 'admin/kp/company/add', companyInfoData, (res) => {
                this.companyNow = this.companyInfo
                this.showCompanyadd = false
                this.getInfo(this.page)
              })
            }
          } else {
            this.$message.error('提交失败！')
          }
        })
      },
      submitBusiness (name) { //提交业务
        this.$refs[name].validate((valid) => {
          if (valid) {
            let data = { ...this.businessInfo }
            data.trade_id = this.companyNow.id
            if (this.businessStatus) {
              app._post(this, 'admin/kp/business', data, (res) => {
                this.showBusinessadd = false
                this.getInfo(this.page)
              })
            } else {
              app._post(this, 'admin/kp/business', data, (res) => {
                this.$set(this.tableData, this.index, { ...data })
                this.showBusinessadd = false
              })
            }
          } else {
            // this.$message('提交失败！');
            console.log(1)
          }
        })
      },
      editorBusiness (index) { //修改业务按钮
        this.index = index
        this.businessStatus = false
        this.showBusinessadd = true
        this.businessInfo = { ...this.$data.tableData[index] }
      },
      deleteBusiness (index) { // 删除业务按钮
        app._delete(this, `admin/kp/business/${this.tableData[index].id}`, {}, (res) => {
          this.tableData.splice(index, 1)
        })
      },
      // 实操系统答案绑定
      toPracticalAnswer () {
        // this.$router.push({ name: 'login', query: { trade_id: this.companyNow.id, status: 'teacher' } })
        window.open(`/kaipiao/#/login/teacher/${this.companyNow.id}`)
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      timeOver () {//选定完时间以后 生成数据
        let month = this.companyInfo.shenbao_month + '-01'
        var monthStartDate = this.strToDate(month)
        this.companyInfo.income_start_time = month
        this.companyInfo.income_end_time = this.getCurrentMonthLastDay(month)
        this.companyInfo.shenbao_time = this.getCurrentMonthLastDay(month)
        //本月的结束时间
        this.companyInfo.jiaokuan_time = this.getNextMonth(month)
        this.companyInfo.youxiao_start_time = month
        this.companyInfo.youxiao_end_time = this.getCurrentMonthLastDay(month)
        this.companyInfo.jiaokuan_end_time = this.companyInfo.jiaokuan_time.substring(0, 8) + '15'
      },
      // 分页控制
      changepage (index) {
        // 当前页码
        this.page = index
        this.getInfo(index)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../public/css/admin.css";
  @import "../../../public/css/element-variables.scss";

  $green-color: #93c343;
  $green-hover-color: #a9cf69;
  a {
    cursor: pointer;
  }

  .company-name {
    font-size: 13px;
    color: #515a6e;
  }

  .business-right {
    text-align: center;
  }

  a:hover {
    color: $green-color;
  }

  .green-btn:hover {
    color: $green-color;
    border-color: $green-hover-color;
    background-color: rgba(147, 195, 67, 0.1);
  }

  .company-top {
    display: flex;
    justify-content: space-between;
  }

  .header-span {
    font-size: 13px;
    color: #030303;
    font-weight: 700;
  }
</style>
<style>
  .companyInfo .el-form-item .el-form-item__label {
    text-align: right;
    vertical-align: middle;
    float: left;
    font-size: 12px;
    color: #515a6e;
    /* line-height: 1;
      padding: 10px 12px 10px 0;
      box-sizing: border-box; */
  }

  .companyInfo .el-form-item {
    margin-bottom: 18px;
  }

  .el-pager li.active {
    color: #93c343;
  }

  .tableHead th {
    background-color: #f8f8f9;
  }

  .tableHead th .cell {
    color: #515a6e;
    font-size: 12px;
    font-weight: 700;
  }

  .el-table--small td.business-cell {
    padding-left: 10px;
  }

  .el-table_1_column_1 {
    padding-left: 10px !important;
  }

  .el-upload__input {
    display: none !important;
  }
</style>
