// 用来控制弹窗的js
export var popData = {
  showCompanyadd: false,
  showBusinessadd: false,
  companyStatus: true, // 是否正在修改 true表示新增状态，false表示修改状态
  businessStatus: true // true表示新增状态，false表示修改状态
}

export var popMethod = {
  showCompanyaddoff () {
    this.showCompanyadd = false
  },
  addcompany () {
    this.showCompanyadd = true
  },
  showBusinessaddoff () {
    this.showBusinessadd = false
  },
  addBusiness () {
    this.showBusinessadd = true
  }
}

